
.wp-block-blocks-course-team-members {
	display: flex;
	flex-wrap: wrap;
	margin-left: -10px;
	margin-right: -10px;

	@for $i from 1 through 6 {

		&.has-#{$i}-columns {
			
			.wp-block-blocks-course-team-member{
				width: calc( 100% / #{$i} - 20px  );
			}
		}
	}
	.wp-block-blocks-course-team-member{
		margin: 10px; 
		background: #ccc;
		padding: 1em;

		img{
			margin-bottom: 20px;
		}

		h4{
			color: #222;
			font-size: 22px;
			font-weight: bold;
			margin: 0px;
		}

		p{
			color: #565656;
			font-size: 16px;
			font-weight: 300;
			margin: 5px 0px;
		}
	}

} 